body .logo {
  width: 15rem;
}
body .logo * {
  width: 100%;
}

.btns-container .btn {
  font-size: 1rem;
  outline: none;
  cursor: pointer;
  text-transform: uppercase;
  padding: calc(1em + 2px) calc(2.5em + 2px);
  border-radius: 0.4rem;
  transition: 0.3s;
  border: none;
  font-weight: 700;
  -webkit-box-reflect: below 4px linear-gradient(to bottom, rgba(0, 0, 0, 0), rgba(0, 0, 0, 0.4));
}
.btns-container .cta {
  color: #f1faee;
  background-color: #1d3557;
  box-shadow: 0px 0px 60px #1f4c65;
}
.btns-container .cta:active {
  scale: 0.92;
}
.btns-container .cta:hover {
  background: #1d3557;
  background: linear-gradient(270deg, rgba(2, 29, 78, 0.6392156863) 0%, #a8dadc 60%);
  color: rgb(4, 4, 38);
}
.btns-container .login {
  color: #f1faee;
  padding: 1em 2.5em;
  background: transparent;
  border: 2px solid #1d3557;
}